* {

  box-sizing: border-box;
  margin: 0;
  padding: 0;
  //border: solid 1px #333;
  -webkit-appearance: none;
}

a{


  color: rgba(map-get($color-index, main_red), 1);

  :hover{

    color: rgba(map-get($color-index, main_blue), 0.8);
  }
}






.hide{

  display:none;

}
.show{

  display:block;

}

.grecaptcha-badge{

  z-index: 5000 !important;
  bottom: 5px !important;
}

.hidden{

  display:none;
}

.dashicons-star-filled::before, .stars i {

  color:rgba(map-get($color-index, orange), 1);

}

.btn.focus, .btn:focus, .btn:hover {
  color: #fff !important;
  text-decoration: none;
}