.activities{

    @include flexbox();

    @include flex-direction(column);

  .label{

        
        @include flexbox();
        max-width: 250px;
        align-items: center;

        @include justify-content(space-between);

        color: #333;
    }


    @import '../../sass_admin/partials/ui_toggle';

    div.ui-toggle {
        @include flexbox();
        align-items: center;
       
        label div{
        width: 16px!important;
        height: 18px;
    }

    
}



}

.aid_form label{

    padding: 0!important;
}

.btn_container{

    display:flex;

    justify-content: space-between;

label , button{

    width: 48%;
    font-family: 'Archivo Black', sans-serif;
    //font-family: 'Roboto', sans-serif!important;

    font-size: 10px;

    text-transform: uppercase;

    display: flex;

    align-items: center;

    span{

        margin: 0 auto;
    }

    

}

}