.border{

    border: none;
}

.headnav{


/*position*/
    position: absolute;
    bottom: 140px; 
    right: 120px;

    z-index: 100;

@include flexbox();

    &__search{


            @extend .border;

            /*presets*/

            padding: 0;
            margin: 0;


            /*size*/    
            width: $btn-width;
            height: $btn-width;

            /*design*/


            background-color: rgba(map-get($color-index, main_blue), 1);
            .svg-icon{

                 fill: #fff;
            }
           
            
            }

    &__subscribe{

        @extend .border;
        /*flex*/
        @include flex(4);

        /*size*/    
        @include flexbox();
        height: $btn-width;
        align-items: center;
        background-color: rgba(map-get($color-index, main_grey), 1);
        color:#fff!important;
        text-decoration: none!important;
        
    }     
    
    &__loginout{
        /*flex*/
        @include flex(1);

        a{

        text-decoration: none;    
        /*size*/   
        @include flexbox(); 
        width: $btn-width;
        height: $btn-width;
        align-items: center;
        
        background-color: rgba(map-get($color-index, main_red), 1);

        i.fa{
            margin: 0 auto;
            color: #fff;
            
        }
        }

    }
      

}