
.btn{

@each $bgcolor in $color-index {
  &--#{nth($bgcolor,1)} {
    background-color: nth($bgcolor,2);

    &:hover {
      background: darken(nth($bgcolor,2), 8%);
      transition: all 0.3s ease;
    }
    &:active {
      background: lighten(nth($bgcolor,2), 2%);
      color: #fff;

    }
  }
}
}


