.aid {

    padding: 10px;

    @include bp(medium) {
        padding: 0;
        max-width: 1200px;
        margin: 0 auto;
    }

    &_form {


        padding-bottom: 150px;

        label {

            padding: 5px 0;
        }

        input[type="button"] {

            padding: 7px;
            margin-top: 8px;
            border: none;

            @include bp(medium) {
                width: auto;
            }
        }


        .submit {

            text-align: center;
            width: 100%;

            border: 0;
            padding: 8px;

            @include bp(medium) {

                width: auto;
            }
        }

        input,
        select,
        button {

            padding: 3px;



        }

        select {

            width: 100%;
            padding: 0.5rem ;
        }

        button {
            width: auto;
            border: 0;
            padding: 8px;
        }



        fieldset {

            margin: 10px 0;
            background-color: #fff;
        }

        .cards {

            @include flexbox();
            @include flex-wrap(wrap);
            @include flex-direction(row);
            @include justify-content(space-between);
            @include align-content(center);

            &__card {

                width: 100%;

                @include bp(medium) {
                    @include flex(1);
                    max-width: 49%;
                }
            }

        }
    }



    .contacts {

        position: relative;

        padding: 8px;

        @include bp(medium) {
            padding: 0;
        }

        &:nth-child(odd) {
            background: rgba(map-get($color-index, main_grey), 0.3);

        }

        &:nth-child(even) {
            background: rgba(map-get($color-index, main_grey), 0.1);
        }



        @include flexbox();
        @include flex-wrap(wrap);
        @include flex-direction(row);
        @include justify-content(space-around);
        @include align-content(center);

        

        &__name,  &__title, &__email, &__phonenumber {


            width: 100%;

            position: relative;

            @include bp(medium) {

                margin: 10px;
                
                @include flex(1);
                @include flex-basis(40%);
            }

        }

        &__name__remove {


            position: absolute;
            top: 20px;

            @include bp(medium) {
                top: 10px;
            }

            right: 5px;
            color: rgba(map-get($color-index, main_red), 1);

            cursor: pointer;



        }

    }



    label[for*="documen"] {
        margin-top: 10px;
        display: inline-block;
        padding: 8px;
        text-align: center;
        width: 100%;

        @include bp(medium) {
            @include flex(1);
            width: auto;
        }


    }


    .file_label {

        border: solid 1px #333;

        margin: 15px 0;
        padding: 5px!important;


        &:hover {

            cursor: pointer;
        }

    }



    .children {

        .kids.kidsaid:first-of-type {

            .dashicons.dashicons-trash {



                display: none
            }
        }


        .kidsaid {

            position: relative;

            border: solid 3px rgba(map-get($color-index, main_red), 1);

            margin: 5px 0;

            &:nth-child(odd) {
                background: rgba(map-get($color-index, main_red), 0.3);
            }

            &:nth-child(even) {
                background: rgba(map-get($color-index, main_red), 0.1);
            }
        }



        .padding {

            padding: 10px;
            margin: 0;
            border: solid 1px #333;


        }



        .kids {
            position: relative;

            @include flexbox();
            @include flex-wrap(wrap);
            @include flex-direction(row);
            @include justify-content(space-between);
            @include align-content(center);


            &__age {

                @extend .padding;

                width: 100%;

                @include bp(medium) {
                    @include flex(2);

                }

                input[type="date"] {

                    width: 100%;

                    @include bp(medium) {
                        width: auto;

                    }
                }

            }

            &__gender {
                @extend .padding;

                @include flex(2);


                @include bp(medium) {

                    @include flex(2);
                    text-align: center;

                }

                .field-container {

                    margin-top: 25px;
                    width: 100%;
                    
                }

                label {
                    display: inline-block;
                    width: 45%;
                }

                input[type="radio"] {


                    margin: 5px;
                }



            }

            &__name {


                @extend .padding;

                width: 100%;

                @include bp(medium) {
                    @include flex(8);
                }




            }




            &__name__remove {



                position: absolute;
                top: 8px;
                right: 8px;
                color: rgba(map-get($color-index, main_red), 1);

                cursor: pointer;



            }




        }
    }
}